.categories{
  margin-bottom: 2.6875em;
  &__inner{
    display: grid;
    grid-template-columns: repeat(9,calc(100% / 9 - 1.5em * 8 / 9));
    gap: 1.5em;
    padding: .625em 1.44em;
    border-radius: 0.75em;
    background-color: $primary;
  }
}

.category-card{
  border-radius: 0.75em;
  color: #fff;
  &:hover{
    color: rgba(#fff,.7);
  }
  &__img{
    border-radius: 0.75em;
    @include imgCover(28.2%);
    filter: blur(2.5px);
  }
  &__title{
    @include centerAbsolute;
    width: 100%;
    text-align: center;
    z-index: 2;
  }
}

.banner{
  margin-top: 2.69em;
  &._mt0,
  &:first-child{
    margin-top: 0;
  }
  &__inner{
    display: block;
    position: relative;
    &:hover{
      color: $black;
    }
    img{
      width: 100%;
      display: block;
      border-radius: .75em;
    }
  }
  &__badge{
    position: absolute;
    top: 1.25em;
    left: 1.25em;
    padding: 0 1.0625em;
    border-radius: 0.75em;
    display: flex;
    align-items: center;
    height: 1.625em;
    text-transform: uppercase;
    font-weight: 300;
    background-color: $gray;
  }
}

.main-news{
  margin: 2.81em 0 0;
  &__inner{
    display: grid;
    grid-template-columns: calc(100% - 38.625em - 1.25em) 38.625em;
    gap: 1.25em;
    align-items: flex-start;
  }
}

.news-sidebar{
  padding-top: .625em;
  @media screen and (min-width: 993px){
    grid-row: 1/3;
    grid-column: 2/3;
  }
  &__items{
    margin-top: 3.25em;
    @include grid100gap(2.81em);
  }
  &__nav{
    display: flex;
    justify-content: flex-end;
    margin-top: 3.875em;
    .m-btn{
      width: 22.4375em;
      max-width: 100%;
    }
  }
}

.news-banners{
  overflow: hidden;
  position: relative;
  &__nav{
    position: absolute;
    bottom: 0;
    left: 0;
    z-index: 3;
    display: flex;
    align-items: center;
    padding: 2.5em 3em;
  }
  .swiper-pagination,
  .swiper-button{
    position: static;
  }
  .swiper-pagination{
    width: auto;
    margin: 0 .25em;
    &-bullet{
      width: .625em;
      height: .625em;
      margin: 0 .5em !important;
      opacity: .41;
      background-color: #fff;
      &-active{
        opacity: 1;
      }
    }
  }
  .swiper-button{
    width: 1.5625em;
    height: auto;
    @include center;
    margin: 0;
    svg{
      width: 100%;
      height: auto;
    }
    &:hover{
      path{
        fill: $primary;
      }
    }
  }
}

.slider{
  .swiper{
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      height: auto;
    }
  }
}

.news-banner{
  padding: 4em 3em;
  position: relative;
  z-index: 2;
  color: #fff;
  min-height: 33.375em;
  height: 100%;
  &__img{
    pointer-events: none;
    border-radius: 0.75em;
    position: absolute;
    height: 100%;
    width: 100%;
    left: 0;
    top: 0;
    img{
      object-fit: cover;
      display: block;
      width: 100%;
      height: 100%;
      border-radius: inherit;
    }
    &:after{
      background: linear-gradient(90deg, rgba(0, 0, 0, 0.73) 29.63%, rgba(0, 0, 0, 0.28) 116.32%, rgba(0, 0, 0, 0.00) 170.79%);
    }
  }
  &__content{
    position: relative;
    z-index: 4;
    max-width: 37em;
  }
  &__desc{
    margin-top: 1.44em;
    color: rgba(#fff,.75);
  }
}

.news-items{
  display: grid;
  grid-template-columns: repeat(3,calc(100% / 3 - 1em));
  gap: 1.125em 1.5em;
}

.news-card{
  display: flex;
  flex-direction: column;
  height: 100%;
  padding: .625em;
  border-radius: 0.75em;
  background: #FFF;
  box-shadow: 0 0 2em 0 rgba(0, 0, 0, 0.07);
  &__img{
    border-radius: 0.75em;
    display: block;
    @include imgCover(55.9%);
  }
  &__content{
    padding: 1em .375em;
    @include grid100gap(1em);
  }
  &__title{
    font-size: 1em;
    line-height: 119%;
    font-weight: 500;
  }
  &__desc{
    @include truncate(2);
    color: $black75;
  }
  &__bottom{
    margin-top: auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: .875em 1em;
    border-radius: 0.75em;
    background-color: $gray;
  }
}

.avatar{
  width: 2.75em;
  height: 2.75em;
  border-radius: .75em;
  flex-shrink: 0;
  img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: inherit;
    display: block;
  }
}

.author{
  display: flex;
  align-items: center;
  .avatar{
    margin-right: .625em;
  }
  &__info{
    @include grid100gap(.25em);
  }
  &__name{
    font-size: .875em;
    line-height: 157%;
    font-weight: 500;
    color: #3E3232;
  }
  &__date{
    font-size: 0.75em;
    line-height: 150%;
    letter-spacing: 0.01563em;
    color: $black75;
  }
}

.favourite-btn{
  width: 1.25em;
  @include center;
  svg{
    width: 1em;
    height: auto;
  }
  &:hover{
    path{
      stroke: $primary;
    }
  }
  &._added{
    path{
      stroke: $primary;
      fill: $primary;
    }
  }
}

.line-title{
  display: flex;
  align-items: center;
  &:before{
    content: '';
    flex-shrink: 0;
    width: 0.25em;
    height: 0.625em;
    border-radius: 0.75em;
    margin-right: .375em;
    background-color: $primary;
    transition: all .3s;
  }
}

.news-preview{
  @include grid100gap(.56em);
  color: rgba(0, 0, 0, 0.75);
  padding-bottom: 1.125em;
  position: relative;
  &:before{
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 14.50056em;
    height: 1px;
    background: rgba(0, 0, 0, 0.30);
    max-width: 100%;
  }
  &__type{
    font-size: 0.9375em;
    line-height: 133%;
    color: $primary;
    letter-spacing: 0.01563em;
  }
  &__title{
    font-size: 1.125em;
    line-height: 111%;
    font-weight: 500;
    letter-spacing: 0.01563em;
  }
  &__bottom{
    margin-top: .625em;
  }
  &__bottom,
  &__comments{
    display: flex;
    align-items: center;
  }
  &__comments{
    img{
      width: 1.25em;
      margin-right: .75em;
      flex-shrink: 0;
    }
    span{
      letter-spacing: 0.01563rem;
      font-size: .9375em;
      line-height: 133%;
    }
  }
}


@media screen and (max-width: 1200px){
  .category-card{
    color: $black;
    &:hover{
      color: $primary;
    }
    &:after{
      display: none;
    }
    &__img{
      filter: blur(1.56298828125px);
      background-color: #fff;
      img{
        display: none;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .categories{
    display: none;
  }

  .banner{
    &__inner{
      img{
        border-radius: .45em;
      }
    }
    &__badge{
      top: .5em;
      left: .5em;
      height: 0.66788em;
      border-radius: 0.46888em;
      padding: 0 .42em;
      span{
        font-size: .55em;
      }
    }
  }

  .main-news{
    margin: 2.81em 0 0;
    &__inner{
      display: grid;
      grid-template-columns: calc(100% - 16.9375em) 16em;
      gap: .6875em .9375em;
    }
  }

  .news-banners{
    grid-column: 1/-1;
    font-size: .617em;
    &__nav{
      padding-bottom: 1em;
      padding-top: 1em;
    }
  }

  .news-banner{
    min-height: 24em;
  }

  .news-items{
    grid-template-columns: repeat(2,calc(50% - .9375em / 2));
    gap: .9375em;
    align-self: flex-start;
  }

  .news-card{
    font-size: .625em;
  }

  .news-sidebar{
    align-self: flex-start;
    .line-title{
      display: none;
    }
    &__items{
      margin-top: 0;
      gap: 1.5em;
    }
    &__nav{
      margin-top: 1.875em;
      .m-btn{
        width: 10.375em;
        height: 2.375em;
        border-radius: 0.46888em;
        span{
          font-size: .7em;
        }
      }
    }
  }

  .news-preview{
    font-size: .61em;
  }
}


@media screen and (max-width: 576px){
  .banner{
    &._hide-mob{
      display: none;
    }
    &__badge{
      left: .25em;
      height: 0.625em;
      border-radius: 0.46888em;
      padding: 0 .25em;
      span{
        font-size: .3125em;
      }
    }
  }

  .main-news{
    &._mt0{
      margin-top: 0;
    }
    &__inner{
      grid-template-columns: 100%;
    }
  }

  .news-banners{
    font-size: 1em;
    &__nav{
      font-size: .617em;
      width: 100%;
      justify-content: center;
    }
  }

  .news-banner{
    padding: 2em .5em 3em 1.5em;
    min-height: 15em;
    &__img{
      border-radius: 0.46888em;
      &:after{
        background: linear-gradient(90deg, rgba(0, 0, 0, 0.73) 29.63%, rgba(0, 0, 0, 0.28) 116.32%, rgba(0, 0, 0, 0.00) 170.79%);
      }
    }
    &__title{
      font-size: 1.125em;
    }
    &__desc{
      margin-top: .9375em;
      max-width: 18.1875em;
      .text14{
        font-size: .5em;
        line-height: 157%;
        letter-spacing: 0.02em;
      }
    }
  }

  .news-items{
    @include fullScreen;
    overflow-x: auto;
    padding-bottom: .5em;
    display: flex;
    gap: unset;
    .news-card{
      width: 14.0625em;
      flex-shrink: 0;
      @include lastM(0 1em 0 0);
    }
  }

  .news-card{
    font-size: 1em;
    padding: .39em;
    &__img{
      border-radius: 0.46888em;
      padding-bottom: 70.7%;
    }
    &__content{
      padding: .625em .23em;
      gap: .625em;
    }
    &__title{
      font-size: .5625em;
    }
    &__desc{
      font-size: .4375em;
      -webkit-line-clamp: 5;
      letter-spacing: .02em;
    }
    &__bottom{
      padding: .59em .625em;
    }
  }

  .author{
    .avatar{
      width: 1.75em;
      height: 1.75em;
      margin-right: .39em;
      border-radius: 0.46888em;
    }
    &__info{
      gap: 0;
    }
    &__name{
      font-size: .4375em;
      line-height: 190%;
    }
    &__date{
      font-size: .375em;
      line-height: 187%;
    }
  }

  .favourite-btn{
    width: .75em;
    svg{
      width: .625em;
    }
  }

  .news-sidebar{
    &._hide-mob{
      display: none;
    }
  }
}
