.header{
  padding: 2.81em 0;
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__logo{
    display: block;
    width: 10.88em;
    img{
      width: 100%;
      display: block;
    }
  }
  .m-search{
    width: 24.9375em;
    max-width: 100%;
    margin-right: 1.56em;
  }
}

.m-rect{
  width: 3em;
  height: 3em;
  border-radius: 0.75em;
  @include center;
  background-color: $gray;
}

.menu{
  display: flex;
  align-items: center;
  &__favourite{
    margin-left: 4.06em;
    &:hover{
      background-color: $primary;
      path{
        fill: #fff;
      }
    }
    svg{
      width: 1.25em;
      height: auto;
    }
  }
}

.menu-user{
  display: flex;
  align-items: center;
  &__img{
    margin-right: .5em;
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
      border-radius: inherit;
    }
  }
  &__btn{
    margin-left: .5em;
    @include center;
    width: 1em;
    svg{
      width: 100%;
      height: auto;
    }
    &:hover{
      path{
        fill: $primary;
      }
    }
  }
}

.m-search{
  position: relative;
  &__more,
  &__confirm{
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    width: 1.25em;
    @include center;
    svg{
      width: 100%;
      height: auto;
    }
    &:hover{
      path{
        fill: $primary;
      }
    }
  }
  &__more{
    left: 1em;
  }
  &__confirm{
    right: 1em;
    width: 1.375em;
  }
  &__field{
    input{
      padding: 0 4.67em;
    }
  }
}


.mob-menu,
.mob-menu__bg,
.burger{
  display: none;
}

.tab-content{
  display: none;
  &._active{
    display: block;
  }
}


@media screen and (max-width: 992px){
  .header{
    padding: 1.44em 0;
    &__logo{
      width: 6.8125em;
    }
    .m-search{
      width: 15.6em;
      margin-right: 1.56em;
    }
  }

  .m-rect{
    width: 1.875em;
    height: 1.875em;
    border-radius: 0.47em;
  }

  .menu{
    &__favourite{
      margin-left: 2.58em;
      svg{
        width: .875em;
      }
    }
  }

  .menu-user{
    &__img{
      margin-right: .3125em;
    }
    &__btn{
      width: .6875em;
      margin-left: .3125em;
    }
  }

  .m-search{
    &__confirm,
    &__more{
      width: .875em;
    }
    &__more{
      left: .625em;
    }
    &__confirm{
      right: .625em;
    }
  }
}


@media screen and (max-width: 576px){
  .header{
    padding: 1.69em 0 1.56em;
    position: relative;
    z-index: 10;
    &__inner{
      justify-content: center;
      position: relative;
    }
    &__logo{
      width: 9em;
    }
    &._open{
      .mob-menu{
        opacity: 1;
        pointer-events: unset;
        &__bg{
          display: block;
        }
      }
    }
  }

  .menu{
    display: none;
  }

  .burger{
    @include center;
    width: 2.3125em;
    height: 2.3125em;
    position: absolute;
    top: 50%;
    right: 0;
    transform: translateY(-50%);
    border-radius: 0.125em;
    img{
      width: 1.5em;
    }
  }

  .mob-menu{
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    min-height: 22.0625em;
    background: #FFF;
    box-shadow: 0px 0px 32px 0px rgba(0, 0, 0, 0.07);
    z-index: 11;
    display: flex;
    flex-direction: column;
    padding: .81em .75em .69em;
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
    &__bg{
      background: rgba(0, 0, 0, 0.50);
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      height: $vh;
      z-index: 10;
    }
    &__tabs{
      display: flex;
      margin-top: auto;
      padding-top: .375em;
    }
    &__title,
    &__link{
      display: flex;
      align-items: center;
      &:before{
        content: '';
        flex-shrink: 0;
        width: 0.15631em;
        height: 0.39075em;
        border-radius: 0.46888em;
        margin-right: .23em;
        background-color: $primary;
        transition: all .3s;
      }
    }
    &__title{
      padding: 1em 0;
      border-bottom: 1px solid #E6E6E6;
      margin-bottom: .5em;
    }
    &__items{
      width: calc(100% + 1.5em);
      margin-left: -.75em;
      padding-right: .25em;
    }
    &__links{
      @include grid100gap(.25em);
      padding-right: .375em;
      max-height: 14em;
      overflow-y: auto;
      @include scroll;
    }
    &__link{
      padding: 0 .875em;
      height: 3.3125em;
      &:not(._active){
        &:before{
          opacity: 0;
          margin: 0;
          width: 0;
        }
      }
      &._active{
        background: #F8F8F8;
      }
    }
  }

  .menu-tab{
    background-color: #E7E7E7;
    width: 2.3125em;
    height: 2.3125em;
    @include center;
    flex-direction: column;
    font-weight: 500;
    border-radius: .125em;
    color: #fff;
    margin-right: .875em;
    transition: all .3s;
    &._active{
      background-color: $primary;
    }
    &__icon{
      height: .625em;
      margin-bottom: .5em;
      display: flex;
      align-items: flex-end;
      justify-content: center;
      img{
        height: 100%;
        width: auto;
      }
      &._small{
        img{
          height: .5em;
        }
      }
    }
    &__title{
      font-size: .375em;
      letter-spacing: 0.00975em;
    }
  }
}
