.footer{
  padding: 5.94em 0 3em;
  background: #212830;
  color: #fff;
  margin-top: 4.5em;
  &__top{
    display: flex;
    justify-content: flex-end;
  }
  &__logo{
    width: 12.625em;
    display: block;
    margin-right: auto;
    img{
      width: 100%;
      display: block;
    }
  }
  &__bottom{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 1.25em 0;
    border-top: 1px solid rgba(#7E868E,.1);
    margin-top: 3.19em;
  }
  &__links{
    display: flex;
    align-items: center;
  }
  &__link{
    color: #7E868E;
    @include lastM(0 7.8em 0 0);
    span{
      font-size: .75em;
      line-height: 125%;
    }
  }
}


.footer-nav{
  margin-left: 6.25em;
  min-width: 10.6em;
  &__title{
    font-weight: 700;
    span{
      font-size: 1.3125em;
      line-height: 143%;
    }
  }
  &__links{
    margin-top: 2.5em;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__link{
    @include lastM(0 0 1.19em);
  }
  &__social{
    @include lastM(0 0 1.875em);
    display: flex;
    align-items: center;
    svg{
      width: 1.25em;
      height: auto;
      margin-right: .875em;
    }
  }
  &__social,
  &__link{
    span{
      font-size: .875em;
      line-height: 150%;
    }
  }
  a.footer-nav__social{
    &:hover{
      path{
        fill: $primary;
      }
    }
  }
}

.socials{
  display: flex;
  align-items: center;
  &__item{
    width: 1.875em;
    flex-shrink: 0;
    @include lastM(0 1.38em 0 0);
    img{
      width: 100%;
      display: block;
      transition: all .3s;
    }
    &:hover{
      img{
        transform: translateY(-20%);
      }
    }
  }
}


@media screen and (max-width: 992px){
  .footer{
    padding: 2.5em 0 1.25em;
    &__top{
      justify-content: space-between;
    }
    &__logo{
      margin-right: 0;
      width: 8em;
    }
    &__bottom{
      padding-bottom: 0;
    }
    &__link{
      @include lastM(0 1.56em 0 0);
      span{
        font-size: .47em;
      }
    }
  }

  .footer-nav{
    min-width: unset;
    margin: 0;
    &__title{
      span{
        font-size: .82em;
      }
    }
    &__links{
      margin-top: 1.56em;
    }
    &__social,
    &__link{
      @include lastM(0 0 .74em);
      span{
        font-size: .54em;
      }
      svg{
        width: .8125em;
        margin-right: .5em;
      }
    }
    &__social{
      @include lastM(0 0 1em);
    }
  }

  .socials{
    &__item{
      width: 1.1875em;
      @include lastM(0 .86em 0 0);
    }
  }
}


@media screen and (max-width: 576px){
  .footer{
    padding: 3.25em 0 2.4em;
    &__top{
      align-items: center;
      flex-direction: column;
    }
    &__bottom{
      flex-direction: column;
      border: none;
      padding: 0;
      margin-top: 3em;
    }
    &__links{
      display: none;
    }
  }

  .footer-nav{
    text-align: center;
    margin: 2.29em 0 0;
    &__links{
      align-items: center;
    }
  }

  .socials{
    &__item{
      width: 2.04em;
      @include lastM(0 1.5em 0 0);
    }
  }
}
