.chapters{
  &__links{
    margin-top: 1.875em;
  }
}

.chapter-link{
  display: flex;
  align-items: center;
  min-height: 5em;
  border-bottom: 1px solid rgba(0, 0, 0, 0.30);
  background-image: url(../img/arrow-right.svg);
  background-size: .5625em auto;
  background-repeat: no-repeat;
  background-position: right center;
  &:hover{
    color: $primary;
    border-color: $primary;
  }
  &__title{
    font-size: 1.125em;
    line-height: 111%;
  }
}

.tags{
  position: sticky;
  top: 1em;
  &__items{
    margin-top: 2.625em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .75em));
    gap: 1.69em 1.5em;
    max-width: 34em;
  }
}

.city-live{
  margin-top: 4.5em;
  &__grid{
    margin-top: 2.5em;
    display: grid;
    grid-template-columns: calc(100% - 38.625em - 1.25em) 38.625em;
    gap: 1.25em;
  }
  &__items{
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 2em / 3));
    gap: 1em;
  }
}

.news-card2{
  display: block;
  position: relative;
  color: #fff;
  &__img{
    border-radius: .75em;
    @include imgCover(100%);
    &:after{
      background: linear-gradient(180deg, rgba(0, 0, 0, 0.00) 43.23%, #000 100%);
    }
  }
  &__content{
    position: absolute;
    bottom: 0;
    left: 0;
    padding: 2.25em 1.56em;
    max-width: 15em;
    width: 100%;
  }
  &__title{
    font-size: 1em;
    line-height: 125%;
  }
}

.weather-card{
  &:hover{
    color: #fff;
  }
  &._pink{
    .news-card2__img:after{
      background: linear-gradient(154deg, rgba(113, 28, 251, 0.91) 0%, rgba(251, 28, 148, 0.60) 99%), rgba(0, 0, 0, 0.45);
    }
  }
  .news-card2__img:after{
    background: linear-gradient(154deg, rgba(7, 156, 39, 0.91) 0%, rgba(192, 255, 113, 0.60) 99%), rgba(0, 0, 0, 0.45);
  }
  &__content{
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    padding: 6em 3.4em;
  }
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__info{
    @include grid100gap(.69em);
    text-align: right;
  }
  &__props{
    @include grid100gap(.44em);
  }
  &__prop{
    font-size: .75em;
    line-height: 117%;
  }
  &__city{
    font-size: 1.25em;
    line-height: 115%;
    font-weight: 500;
  }
  &__date{
    font-size: .875em;
    line-height: 143%;
    letter-spacing: 0.01563em;
  }
  &__degree{
    @include center;
    margin-top: 3.25em;
    img{
      width: 3.562em;
      flex-shrink: 0;
      margin-right: 1.06em;
    }
  }
}

.temperature{
  display: flex;
  &__num{
    font-size: 3.0625em;
    line-height: 116%;
  }
  &__measure{
    font-size: 1em;
    line-height: 119%;
    font-weight: 500;
  }
}


@media screen and (max-width: 992px){
  .line-title{
    &:before{
      width: 0.15631em;
      height: 0.39075em;
      margin-right: .23em;
    }
    .text20{
      font-size: .78em;
    }
  }

  .chapters{
    margin-top: 2em;
    grid-column: 1/-1;
    &__links{
      margin-top: 1em;
    }
  }

  .chapter-link{
    min-height: 3em;
    background-size: .3125em auto;
    &__title{
      font-size: .6875em;
    }
  }

  .tags{
    &__items{
      margin-top: 1.625em;
      grid-template-columns: repeat(2,calc(50% - .6875em / 2));
      gap: 1em .6875em;
      max-width: 14.5625em;
    }
  }

  .tag-link{
    padding: 0 .5em;
    height: 2.375em;
    border-radius: 0.46888em;
    span{
      font-size: .6875em;
    }
  }

  .city-live{
    margin-top: 2em;
    &__grid{
      margin-top: 1.4em;
      grid-template-columns: calc(100% - 17em) 16em;
      gap: 1em;
    }
    &__items{
      grid-template-columns: repeat(2,calc(50% - .25em));
      gap: .5em;
      .weather-card{
        order: 2;
      }
    }
  }

  .news-card2{
    font-size: .6875em;
  }

  ._hide-tablet{
    display: none;
  }
}


@media screen and (max-width: 576px){
  .chapters{
    display: none;
  }

  .tags{
    position: static;
    order: -1;
    &__items{
      margin-top: .56em;
      max-width: unset;
      display: flex;
      gap: unset;
      @include fullScreen;
      @include scroll;
      overflow-x: auto;
      padding-bottom: 1em;
      &::-webkit-scrollbar {
        width: 0.1875em;
        height: 0.1875em;
      }
    }
  }

  .tag-link{
    flex-shrink: 0;
    @include lastM(0 .69em 0 0);
  }

  .city-live{
    &__grid{
      grid-template-columns: 100%;
      margin-top: .5em;
    }
    &__sidebar{
      display: none;
    }
  }

  .news-card2{
    font-size: 1em;
    &__content{
      padding: .6875em;
      max-width: 7em;
    }
    &__title{
      font-size: .6875em;
    }
  }

  .weather-card{
    &__content{
      padding: 2.5em .6875em;
    }
    &__top{
      align-items: flex-start;
    }
    &__props{
      gap: .25em;
    }
    &__prop{
      font-size: .5625em;
    }
    &__info{
      gap: .44em;
    }
    &__city{
      font-size: .619em;
    }
    &__date{
      font-size: .43em;
    }
    &__degree{
      margin-top: 1.5em;
      img{
        width: 2.82em;
        margin-right: .83em;
      }
    }
  }

  .temperature{
    &__num{
      font-size: 1.5em;
    }
    &__measure{
      font-size: .5em;
    }
  }
}
